@mixin effect {
  transition-duration: 0.5s;
  outline: none;
  box-shadow: 5px 5px 5px grey;
  transform: translate(0px, -5px);
}

input {
  width: max-content;
  margin: 15px auto;
  padding: 10px 30px;
  transition-duration: 0.5s;
  border: 0.5px solid grey;
  border-bottom: 1px solid grey;

  &:focus {
    @include effect;
  }
  @media only screen and (max-width: 600px) {
    @include effect;
  }
}

input[type='number'] {
  font-size: 1.5em;
}
